.sifra {
  width: 100%;
  margin-top: 5%;
}

.sifra h1 {
  margin-top: 100%;
}

.sifra form {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  margin: auto;
  width: 40%;
}

.sifra form label {
  margin-top: 10px;
}

.sifra .div label {
  margin-top: 10px;
}

.sifra input {
  border-radius: 2px;
  border: 1px solid lightgrey;
  height: 30px;
  padding-left: 10px;
}

.sifra .div input {
  border-radius: 2px;
  border: 1px solid lightgrey;
  height: 30px;
  padding-left: 10px;
}

.sifra .dugme2 {
  border: none;
  height: 40px;
  padding-left: 10px;
  margin-top: 30px;
  background-color: darkblue;
  color: white;
  outline: none;
  width: 100%;
  cursor: pointer;
  border-radius: 3px;
  margin-bottom: 30px;
