  border-radius: 2px;
  border: 1px solid lightgrey;
  padding-left: 10px;
  padding-top: 10px;
  font-family: sans-serif;
}

.sifra .uspDugme, .sifra .uspDugme2 {
  border: none;
  position: absolute;
  margin-top: 10px;
  height: 30px;
  width: 30px;
  border-radius: 50%;
  background-color: #eee;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
