.sifra .uspDugme2:hover {
  color: white;
  background-color: red;
}

.sifra .uspDugme {
  margin-left: 10px;
}

.sifra .uspDugme2 {
  margin-left: 160px;
  margin-right: -10px;
}

.sifra select {
  background-color: white;
  border: 1px solid lightgray;
  height: 30px;
  padding-left: 10px;
  outline: none;
}


