.informacije {
	background-image: url('./pozadina.jpg');
	background-size: cover;
}

@media only screen and (min-width: 990px){
.slika10{
	width: 100%;
	max-height: 986px;
	bottom: 0;
}

.fixano {
	position: fixed;
	top: 0;
	width: 100%;
	opacity: 0.95;
}
.textnas{
	position:absolute;
	background:rgba(255, 255, 255, 0.9);
	border:6px double #65594d;
    top:70%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
   	padding:2% 2% 2% 2%;
    color:#65594d;
    text-align:center;
}

.naslov11{
	position:absolute;
	
    top: 20%;
    left: 50%;
    -webkit-transform: translate(-50%, -20%);
        -ms-transform: translate(-50%, -20%);
            transform: translate(-50%, -20%);
    padding:5% 5% 5% 5%;
    color:#65594d;
    text-align:center;
    
}
}


@media only screen and (max-width: 990px){


	.textnas{
		position:absolute;
		background:rgba(255, 255, 255, 0.9);
		border:6px double #65594d;
		
	
		padding:2% 2% 2% 2%;
		color:#65594d;
		text-align:center;
	}
}




